"use client"

// app/transactions/[id]/page.tsx
import { notFound } from "next/navigation";
import Api from "../../../services/logic"
import { useEffect, useState } from "react";
import { Button } from "@/components/ui/button";
import { Separator } from "@/components/ui/separator";
import {
  Item,
  ItemActions,
  ItemContent,
  ItemDescription,
  ItemTitle,
} from "@/components/ui/item";

interface Props {
  params: { id: string };
}

export default async function TransactionPage({ params }: Props) {
  const { id } = params;
  const api = new Api();

  // Since you don't have a single transaction API, fetch multiple and find one
  const result = await api.getTransactions({ page: 1, limit: 1000 });
  const transaction = result.data.find(
    (tx: any) => String(tx.id) === id
  );

  if (!transaction) {
    return notFound();
  }

  return (
    <div className="max-w-xl p-6 rounded-md border border-gray-300 dark:border-gray-600 dark:bg-[#0c1017] dark:text-white">
      <Item variant="muted">
        <ItemContent>
          <ItemTitle>Transaction ID: {transaction.id}</ItemTitle>
          <ItemDescription>Date: {new Date(transaction.date).toLocaleDateString()}</ItemDescription>
          <Separator className="my-4" />

          {/* Map all properties */}
          <dl className="grid grid-cols-2 gap-x-6 gap-y-3">
            {Object.entries(transaction).map(([key, value]) => (
              <div key={key}>
                <dt className="text-sm font-medium text-gray-500 dark:text-gray-400 capitalize">
                  {key.replace(/_/g, " ")}
                </dt>
                <dd className="mt-1 text-sm text-gray-900 dark:text-gray-200">
                  {String(value)}
                </dd>
              </div>
            ))}
          </dl>

          <Separator className="my-4" />

          <ItemActions>
            <Button asChild>
              <a href={`/transactions/${transaction.id}/edit`} className="bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-500">
                Edit
              </a>
            </Button>
            <Button asChild>
              <a href="/transactions" className="ml-4 px-4 py-2 rounded-lg border border-gray-400 hover:bg-gray-100 dark:hover:bg-gray-700">
                Back to Transactions
              </a>
            </Button>
          </ItemActions>
        </ItemContent>
      </Item>
    </div>
  );
}
